/*------------------------------------*\
    Button
\*------------------------------------*/
.btn{
	/* structure */
	display: inline-block;
	vertical-align: middle;
	position: relative;
	margin: 0;
	height: 36px;
	line-height: 36px;
	padding: 0 32px;
	min-width: 64px;
    box-sizing: border-box;
	/* style */
	font-size: 16px;
	text-align: center;
	font-weight: 300;
 	font-family: Helvetica,Arial,"Noto Sans TC","Helvetica Neue","微軟正黑體","Microsoft JhengHei",PMingLiU,sans-serif;
	letter-spacing: 0;
	text-transform: uppercase;
	text-decoration: none;
	color: #6772e5;
	background-color: #fbfbfb;
	border: 0;
	border-radius: 22px;
	outline: none;
	box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
	/* interact */
	cursor: pointer;
	overflow: hidden;
	-webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
    transition: all .15s ease;
}
.btn--hover,
.btn:hover{
	color: #7795f8;
	transform: translateY(-1px);
	box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}
.btn--active,
.btn:active{
	color: #555abf;
    background-color: #f6f9fc;
    transform: translateY(1px);
    box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
}
.btn.btn--primary,
.btn.btn--secondary,
.btn.btn--positive,
.btn.btn--negative,
.btn.btn--alert{
	color: #fbfbfb;
}
.btn.btn--primary{
	background-color: #3ecf8e;
}
.btn.btn--primary.btn--hover,
.btn.btn--primary:hover{
	background-color: #53e8a5;
	transform: translateY(-1px);
	box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}
.btn.btn--primary.btn--active,
.btn.btn--primary:active{
	background-color: #30ba7c;
	transform: translateY(1px);
	box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 6px rgba(0,0,0,.08);
}
.btn.btn--secondary{
	background-color: #6772e5;
}
.btn.btn--secondary.btn--hover,
.btn.btn--secondary:hover{
	background-color: #7795f8;
	transform: translateY(-1px);
	box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}
.btn.btn--secondary.btn--active,
.btn.btn--secondary:active{
	background-color: #555abf;
	transform: translateY(1px);
	box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 6px rgba(0,0,0,.08);
}
.btn.btn--positive{
	background-color: #00BB91;
}
.btn.btn--positive.btn--hover,
.btn.btn--positive:hover{
	background-color: #00d1a2;
	transform: translateY(-1px);
	box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}
.btn.btn--positive.btn--active,
.btn.btn--positive:active{
	background-color: #01a580;
	transform: translateY(1px);
	box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 6px rgba(0,0,0,.08);
}
.btn.btn--negative{
	background-color: #f84545;
}
.btn.btn--negative.btn--hover,
.btn.btn--negative:hover{
	background-color: #f46161;
	transform: translateY(-1px);
	box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}
.btn.btn--negative.btn--active,
.btn.btn--negative:active{
	background-color: #d63b3b;
	transform: translateY(1px);
	box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 6px rgba(0,0,0,.08);
}
.btn.btn--alert{
	background-color: #f7ba36;
}
.btn.btn--alert.btn--hover,
.btn.btn--alert:hover{
	background-color: #ffcb5b;
	transform: translateY(-1px);
	box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}
.btn.btn--alert.btn--active,
.btn.btn--alert:active{
	background-color: #d8a22f;
	transform: translateY(1px);
	box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 6px rgba(0,0,0,.08);
}
.btn--disabled,
.btn--disabled:hover,
.btn--disabled:active,
.btn--disabled:focus{
	color: #686C71 !important;
	pointer-events: none;
	background-color: #B2BAC4 !important;
}
/* Size */
.btn.btn--lg{
	font-size: 16px;
	line-height: 56px;
	height: 56px;
	font-weight: 400;
	border-radius: 28px;
}
.btn.btn--md{
	font-size: 16px;
	line-height: 44px;
	height: 44px;
}
.btn.btn--sm{
	font-size: 13px;
	line-height: 32px;
	height: 32px;
	padding: 0 16px;
}
/* group */
.btn-group .btn{
	margin-left: 8px;
	margin-right: 8px;
	margin-bottom: 16px;
}
.btn-group .btn:last-child{
	margin-bottom: 0px;
}
.btn-group--center{
	text-align: center;
}
.btn-group--right{
	text-align: right;
}
.btn-group--right .btn{
	margin-right: 0;
	margin-left: 16px;
}
/* social-btns */
.social-btns{
	display: flex;
}
.social-btns .icon-btn{
	display: inline-block;
	border: 1px solid #3ecf8e;
	color: #3ecf8e;
	width: 40px;
	height: 40px;
	text-align: center;
	border-radius: 50%;
	margin-right: 8px;
	transition: all 0.2s;
}
.social-btns .icon-btn:last-child{
	margin-right: 0;
}
.social-btns .icon-btn:hover{
	color: #fbfbfb;
	background-color: #3ecf8e;
}
.social-btns .icon-btn i{
	line-height: 38px;
	font-size: 20px;
}

/*------------------------------------*\
    Navigation
\*------------------------------------*/

.navbar{
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 64px;
	padding: 0 64px;
	box-shadow: 0 4px 6px rgba(50, 50, 93, 11), 0 1px 3px rgba(0,0,0,.08);
}

.navbar--light{
	background-color: #f3f4f5;
	opacity: 0.9;;
}

.navbar__logo{
	display: inline-block;
	width: 32px;
	height: 32px;
	background-image: url('../images/common/logo_g.svg');
}

.navbar__nav{
	display: flex;
}

.navbar__nav .nav__link{
	display: inline-block;
	color: #40414f;
	text-decoration: none;
	line-height: 24px;
	padding: 20px 16px;
	transition: all 0.15s;
}

.navbar__nav .nav__link:hover{
	color: #7795f8;
}

/*------------------------------------*\
    Jumbotron
\*------------------------------------*/

.jumbotron{
	background-image: url(../images/bg/bg001.jpg);
	background-color: #f3f4f5;
	background-position: right;
	background-size: cover;
	background-repeat: no-repeat;
	padding-top: 304px;
	padding-bottom: 240px;
	text-align: center;
}

.jumbotron__heading{
	font-size: 64px;
	color: #28282b;
	margin-bottom: 8px;
}

.jumbotron__para{
	margin-bottom: 24px;
}

.mask--light{
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	background-color: #fbfbfb;
	opacity: 0.4;
}

/*------------------------------------*\
    Image
\*------------------------------------*/

.img-container{
	width: 100%;
	display: block;
	position: relative;
}

.img-container:before{
	content: "";
	display: block;
	width: 100%;
	padding-top: 75%;
	background-color: #b2bac4;
}

.img-container .image{
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;

	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
}

/*------------------------------------*\
    Hero
\*------------------------------------*/

.hero{
	padding-top: 200px;
	padding-bottom: 200px;
}

/*------------------------------------*\
    Hero
\*------------------------------------*/

.card{
	background-clip: #fbfbfb;
	box-shadow: 0 4px 6px rgba(50, 50, 93, 11), 
				0 1px 3px rgba(0,0,0,.08);
	border-radius: 4px;
	overflow: hidden;
}

/*------------------------------------*\
    Footer
\*------------------------------------*/

.footer{
	padding: 80px;
	box-shadow: 0 4px 6px rgba(50, 50, 93, 11),
				0 1px 3px rgba(0,0,0,.08);
	background-color: #fbfbfb;
}
.footer__title{
	font-size: 24px;
	color: #686c71;
}
.footer__description{
	color: #686c71;
}
.footer__item{
	margin-bottom: 8px;
}
.footer__link{
	color: #686c71;
	text-decoration: none;
	transition: all 0.2s;
}
.contact-info{
	margin-bottom: 24px;
}

.contact-info .footer__item:first-child{
	margin-top: 24px;
}